<template>
  <div class="hello">
<!--  展示用户    -->
      <div v-show="info.users.length" class = "card" v-for = "user in info.users" :key="user.login">
        <a :href="user.html_url" target="_blank">
          <img :src="user.avatar_url" style="width: 100px"/>
        </a>
        <p class="card-text">{{user.login}}</p>
      </div>
<!--  展示欢迎词  -->
    <h1 v-show="info.isFirst">欢迎使用查询功能</h1>
<!--  展示加载中  -->
    <h1 v-show="info.isloading">正在加载中....</h1>
<!--  展示错误信息  -->
    <h1 v-show="info.errMsg">发生错误:{{info.errMsg}}</h1>
    <router-link active-class="red" to="/List/List2">List-mesg</router-link>

    <router-view></router-view>

  </div>




</template>

<script>
export default {
  name: 'List',
  data(){
    return{
      info:{
        isFirst:true,
        isloading:false,
        errMsg:'',
        users: []
      }
    }
  },
  methods:{

  },
  mounted() {
    this.$bus.$on('updateListData',(dataObj)=>{
      console.log('我是List组件，我收到了:',dataObj)
      this.info = {...this.info,...dataObj}
    })
  },
  /*beforeDestroy() {
    console.log('组件被销毁')
  }*/
}
</script>

