<template>
  <div class="hello">
    <input type="text" placeholder="enter the name" v-model="keyword">
    <button @click="getUserName">获取个人信息</button>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'HelloWorld',

  props: {
    msg: String
  },
  data(){
    return{
      keyword:''
    }
  },
  methods:{
      getUserName(){
        this.$bus.$emit('updateListData',{
          users:[],
          isFirst:false,
          isloading:true,
          errMsg:'',
      })
      axios.get(`https://api.github.com/search/users?q=${this.keyword}`).then(
          response =>{
            console.log('请求成功了！！！')
            this.$bus.$emit('updateListData',{
              users:response.data.items,
              isloading:false,
              errMsg:'',
            })
          },
          error => {
            this.$bus.$emit('updateListData',{
              users:[],
              isloading:false,
              errMsg:error,
            })
          }
      )
    }
  }
}
</script>

