import VueRouter from 'vue-router'
import List from "../pages/List";
import Vuexxxx from "../pages/Vuexxxx";
import List2 from "../pages/List2";
import Detail from "@/pages/Detail";



export default new VueRouter({
    routes:[
        {
            name:'List',
            path:'/List',
            component:List,
            children:[
                {
                    name:'List2',
                    path:'List2',
                    component:List2,
                    children:[
                        {
                            name:'Detail',
                            path:'Detail/:id/:title',
                            component:Detail,
                            props:true
                        }
                    ]
                }
            ]
        },
        {
            name:'Vuexxxx',
            path: '/Vuexxxx',
            component:Vuexxxx,

        }
    ]
})